.sidebar {
  background: #286b86;
}
.content {
  flex: 1;
  justify-content: space-between;
}
.content main {
  position: relative;
  flex: 1;
  height: calc(30vh);
  overflow-y: scroll;
  padding: 15px 15px 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 150ms ease-in-out;
  opacity: 0;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
@keyframes fade {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}
img {
  filter: opacity(70%);
  animation: fade 1.5s ease-in-out infinite;
}



