.menu {
  position: relative;
}
.menu.toggled a:not(.handle):hover .tooltip {
  display: inline-block !important;
}
.menu .handler {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-size: 18px;
  cursor: pointer;
}
.menu a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fefefe;
  transition: all 300ms;
  padding: 10px 15px;
  position: relative;
}
.menu a .fa {
  font-size: 20px;
}
.menu a:hover,
.menu a.active {
  color: #85d0e7;
}
.menu a.active:after {
  content: '';
  position: absolute;
  right: -15px;
  border: 6px solid transparent;
  border-right-color: #e7e7e7;
  cursor: default;
}
.menu a .tooltip {
  display: none;
  position: absolute;
  white-space: nowrap;
  left: 45px;
  z-index: 99;
  padding: 2px 4px;
  border-radius: 3px;
  background: #85d0e7;
  color: #286b86;
}



