


































































	header {
		min-height: 70px;
		max-height: 70px;
		padding: 15px;
		background: #ffffff;
        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
        z-index: 100;
		position: relative;

		a,
		a:active,
		a:focus,
		a:hover {
			text-decoration: none;
			color: #777;
		}

		img {
			filter: opacity(70%);
		}

		.logout-button {
			padding: 5px;
			border: solid 1px lighten(tomato, 10%);
			border-radius: 3px;
			color: lighten(tomato, 10%);
			font-size: 12px;

			&:hover {
				border-color: tomato;
				color: tomato;
			}
		}

		.env-label {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
			background: tomato;
			color: #ffffff;
			padding: 1px 5px;
			z-index: 9999;
		}

		.fade-leave-active {
			transition: opacity 1s
		}

		.fade-leave-to {
			opacity: 0
		}
	}



