header {
  min-height: 70px;
  max-height: 70px;
  padding: 15px;
  background: #ffffff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
  z-index: 100;
  position: relative;
}
header a,
header a:active,
header a:focus,
header a:hover {
  text-decoration: none;
  color: #777;
}
header img {
  filter: opacity(70%);
}
header .logout-button {
  padding: 5px;
  border: solid 1px #ff8e7a;
  border-radius: 3px;
  color: #ff8e7a;
  font-size: 12px;
}
header .logout-button:hover {
  border-color: tomato;
  color: tomato;
}
header .env-label {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: tomato;
  color: #ffffff;
  padding: 1px 5px;
  z-index: 9999;
}
header .fade-leave-active {
  transition: opacity 1s;
}
header .fade-leave-to {
  opacity: 0;
}



