



























































.menu {
	position: relative;

	&.toggled {
		a:not(.handle):hover {
			.tooltip {
				display: inline-block !important;
			}
		}
	}

	.handler {
		height: 70px;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #ffffff;
		font-size: 18px;
		cursor: pointer;
	}

	a {
		display: flex;
		align-items: center;
		text-decoration: none;
		color: darken(#ffffff, 0.5);
		transition: all 300ms;
		padding: 10px 15px;
		position: relative;

		.fa {
			font-size: 20px;
		}

		&:hover,
		&.active {
			color: lighten(#5bc0de, 10);
		}

		&.active:after {
			content: '';
			position: absolute;
			right: -15px;
			border: 6px solid transparent;
			border-right-color: #e7e7e7;
			cursor: default;
		}

		.tooltip {
			display: none;
			position: absolute;
			white-space: nowrap;
			left: 45px;
			z-index: 99;
			padding: 2px 4px;
			border-radius: 3px;
			background: lighten(#5bc0de, 10);
			color: #286b86;
		}
	}
}



