











































































.box {
	width: 500px;
	padding: 0;
	background: #ffffff;
	margin: 200px auto;

	.side {
		width: 30%;
		padding: 15px;
		border-right: solid 1px #f1f1f1;
		background: #f7f7f7;
		border-radius: 3px 0 0 3px;

		img {
			filter: opacity(70%);
			margin-bottom: 5px;
		}
	}

	.content {
		width: 70%;
		padding: 15px;
	}
}



